@import url("https://fonts.googleapis.com/css2?family=Monoton&family=Quicksand:wght@500;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 2.4rem;
  font-family: sans-serif;
  color: #5a3e2b;
  font-family: "Quicksand";
  font-weight: 500;
}

.app {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-rows: auto auto 1fr auto;
}

h1 {
  text-align: center;
  background-color: #f4a226;
  font-family: "Monoton";
  font-size: 8rem;
  text-transform: uppercase;
  font-weight: 400;
  word-spacing: 30px;
  letter-spacing: -5px;
  padding: 2.4rem 0;
}

.add-form {
  background-color: #e5771f;
  padding: 2.8rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.8rem;
}

h3 {
  margin-right: 1.6rem;
  font-size: 2.4rem;
}

button,
select,
input {
  background-color: #ffebb3;
  color: #5a3e2b;
  font-family: inherit;
  border: none;
  border-radius: 10rem;
  padding: 1.2rem 3.2rem;
  font-weight: 700;
  font-size: 1.8rem;
  cursor: pointer;
}

.add-form button {
  text-transform: uppercase;
  background-color: #76c7ad;
}

.list {
  background-color: #5a3e2b;
  color: #ffebb3;
  padding: 4rem 0;

  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 3.2rem;
  align-items: center;
}

.actions button,
.list select {
  text-transform: uppercase;
  padding: 0.8rem 2.4rem;
  font-size: 1.4rem;
  font-weight: 700;
  margin: 0 0.8rem;
}

.list ul {
  list-style: none;
  width: 80%;
  overflow: scroll;

  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.2rem;
  justify-content: center;
  align-content: start;
}

.list li {
  display: flex;
  align-items: center;
  gap: 1.2rem;
}

input[type="checkbox"] {
  height: 2rem;
  width: 2rem;
  accent-color: #e5771f;
}

.list li button {
  cursor: pointer;
  background: none;
  border: none;
  font-size: 1.8rem;
  padding: 0.8rem;
  transform: translateY(2px);
}

.stats {
  background-color: #76c7ad;
  text-align: center;
  font-weight: 700;
  padding: 3.2rem 0;
}

.box {
  position: relative;
  font-family: "Quicksand", sans-serif;
  font-weight: 900;
  display: flex;
  align-items: center;
  padding: 10px 16px;
  background-color: rgb(145, 132, 19);
  border-radius: 12px;
  margin: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease;
}

.box:hover {
  background-color: rgb(168, 152, 19);
}

input[type="checkbox"] {
  appearance: none;
  position: absolute;
  top: 50%;
  left: 8px;
  transform: translateY(-50%);
  width: auto;
  height: 5rem;
  border: 2px solid #311919;
  border-radius: 8px;
  background-color: rgb(131, 104, 104);
  cursor: pointer;
  margin-right: 12px;
  transition: background-color 0.2s ease;
}

input[type="checkbox"]:checked {
  background-color: rgb(19, 124, 143);
  border-color: rgb(19, 124, 143);
}

input[type="checkbox"]:checked::after {
  content: "âœ”";
  position: absolute;
  left: 23px;
  top: 9px;
  color: white;
  font-size: 18px;
  transform: scale(1.8);
}

/* Text part */
.text {
  margin-left: 1px;
  font-size: 1.8rem;
  font-weight: 600;
  color: #fff;
  flex-grow: 1;
  padding-right: 20px;
}

.text2 {
  margin-left: 80px;
  /* font-size: 1.8rem; */
  /* font-weight: 600; */
  color: #fff;
  flex-grow: 1;
}

/* Cancel button */
.cancel {
  font-size: 18px;
  color: #e03e3e;
  background: transparent;
  border: none;
  padding: 5px;
  cursor: pointer;
  transition: color 0.3s ease, transform 0.3s ease;
}

.cancel:hover {
  color: #b31e1e;
  transform: scale(1.1);
}

.cancel:active {
  transform: scale(0.95);
}

.actions {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-top: 2rem;
}

.actions select,
.actions button {
  text-transform: uppercase;
  padding: 0.8rem 2.4rem;
  font-size: 1.4rem;
  font-weight: 700;
  margin: 0 0.8rem;
}

/* List styling */
.list {
  background-color: #5a3e2b;
  color: #ffebb3;
  padding: 4rem 0;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 3.2rem;
  align-items: center;
}

.list ul {
  list-style: none;
  width: 80%;
  overflow: scroll;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.2rem;
  justify-content: center;
  align-content: start;
}
